    </svg>
  </div>
</template>

<script>
    export default {
        /**
         * КОМПОНЕНТ ИКОНИ ДОМОЙ
         * @param:
         *        size      - размер иконки
         *        color     - цвет иконки
         *
         * ПРИМЕР ВЫЗОВА ИКОНКИ
         *                      <div class="icon" style="width: 100%; height: 50px;">
         *                             <icon-navigation-home :color="'red'"/>
         *                       </div>
         */
        name: "icon__settings",
        props: {
            size: {
                type: Number,
                default: 24
            },
            color: {
                type: String,
                default: '#909090'
            }
        },
        data() {
            return {
                colorIcon: '#909090',
                testBool: false
            }
        },
        methods: {
            inColor() {
                this.colorIcon = '#4A90E2';
            },
            outColor() {
                this.colorIcon = this.color;
            }
        },
        mounted() {
            this.colorIcon = this.color;
        }
    }
</script>

<style lang="less">
  #navigationHomeTestings {
    position: relative;

    > svg {
      position: absolute;
    }
  }
</style>









