import Axios from 'axios'

const state      = {
  /**
   * учетные записи в блоке администратирования
   */
  administrationUsers: null,
};
const getters    = {
  //геттер всех учётных записей в админке
  ADMINUSERS: state => {
    return state.administrationUsers
  },
};
const mutations  = {
  //сеттер всех учетных записей для админки
  SET_ADMINUSERS: (state, data) => {
    state.administrationUsers = data
  },
  //сеетер удаления пользователя из хранилища
  SET_DELETEUSER: (state, id) => {
    state.administrationUsers.forEach((el, i) => {
      if (el.id == id) {
        state.administrationUsers.splice(i, 1)
      }
    })
  },
};
const actions    = {
  /**
   * дейстивие по удлаению пользователя
   * @param state
   * @param id
   * @returns {Promise<void>}
   * @constructor
   */
  DELETE__USER: async (state, id) => {
    let {data} = await Axios.delete(`http://localhost:8081/deleteUsers?id=${id}`);
    state.commit('SET_DELETEUSER', id)
  },
};

export default {state, getters, mutations, actions}



