<template>
  <div id="adminka">
    <div class="adm">
      <div class="adm-header">
        <span>Администрирование</span>
      </div>
      <div
        v-if="this.$store.getters.ADMINUSERS !== null"
        class="adm-table">
        <div class="adm-table__search">
          <search
            @search="searchData"
          />
        </div>
<!--        <MC__table-->
<!--          v-if="search.length > 0"-->
<!--          :data-table="search"-->
<!--          :name-table="{nameDB: 'supportsystemchimical', nameTable: 'supportmessage'}"-->
<!--          :number-row="10"-->
<!--          :name-id-recipient="'id'"-->
<!--          :settings="['просмотреть','удалить','ответить','редактировать']"-->
<!--        />-->
                <div class="adm-table__header">
                  <div class="adm-table__header-FIO"><span>ФИО</span></div>
                  <div class="adm-table__header-login"><span>Логин</span></div>
                  <div class="adm-table__header-mail"><span>Почта</span></div>
                  <div class="adm-table__header-phone"><span>Телефон</span></div>
                  <div class="adm-table__header-status"><span>Статус</span></div>
                  <div class="adm-table__header-position"><span>Должность</span></div>
                  <div class="adm-table__header-role"><span>Роль</span></div>
                </div>
                <div class="adm-table__body">
                  <column-user
                    v-for="(user,i) in search"
                    :ref="i"
                    :key="i"
                    v-if="i++ < numberRec * activePage && i > numberRec * activePage - numberRec"
                    :dataUser="user"
                    :roles="data.roles"
                    @statusUser="statusUserDB"
                  >
                  </column-user>
                  <div style="display: none">
                    <span>numberRec -{{ numberRec }}</span>
                    <span>activePage -{{ activePage }}</span>
                  </div>
                </div>
                <div class="adm-table__pagination">
                  <pagination
                    :amountRecords="this.search.length"
                    :active-page="activePage"
                    @numberRecords="numberRecord"
                    @activePage="activePages"
                  />
                </div>
      </div>
    </div>
  </div>
</template>

<script>
    import getAdminka from '../../../services/router/adminka'
    import ColumnUser from "./column-user";
    import Search from "../../search/search";
    import Pagination from "../../pagination/pagination";
    import getAcessCheck from "../../../services/getAcessCheck";
    import MC__table from "../../mainComponent/SystemTable/table/MC__table";

    export default {
        name: "admininka",
        components: {MC__table, Pagination, Search, ColumnUser},
        data() {
            return {
                data: {
                    users: null,
                    roles: null
                },
                searchTitle: '',
                activePage: 1,
                numberRec: null,
                cashUsers: null
            }
        },
        watch: {
            //если был применен поиск , тогда ставим первую страницу пагинации
            searchTitle() {
                this.activePage = 1
            },
            activePage(data) {
                return this.activePage
            }
        },
        computed: {
            search() {
                if (this.searchTitle) {
                    return this.$store.getters.ADMINUSERS.filter(el => {
                        return el.login.includes(this.searchTitle)
                            || el.nameUser.includes(this.searchTitle)
                            || el.lastName.includes(this.searchTitle)
                            || el.firstName.includes(this.searchTitle)
                    })
                } else {
                    return this.$store.getters.ADMINUSERS
                }
            },
        },
        methods: {
            async getDataAdministration() {
                this.$store.commit('SET_LOADER', true);
                const res = await getAdminka.dataAdminka(
                    this.$store.getters.INFOUSER.id,
                    this.$store.getters.INFOUSER.userInfo.login,
                    this.$store.getters.INFOUSER.userInfo.pass,
                );
                if (res) {
                    if (res.data.status) {
                        this.$store.commit('SET_LOADER', false);
                        this.data.roles = res.data.roles;
                        this.$store.commit('SET_ADMINUSERS', res.data.data);
                        this.data.users = this.$store.getters.ADMINUSERS
                    }
                }
            },
            searchData(data) {
                this.searchTitle = data.search
            },
            //ОТЛАВВЛИВАЕМ НОМЕР СТРАНИЦЫ ОТ КОМПОНЕНТА ПАГИНАЦИИ
            activePages(data) {
                this.activePage = data
            },
            //Ловим от пагинцации, сколько отображать записей на странице
            numberRecord(data) {
                this.numberRec = data
            },
            //МЕТОД ОТЛАВЛИВАНИЯ ИЗМЕНЕНИЙ СТАТУСА
            statusUserDB(data) {
                this.data.users.find(el => el.id === data.id).status = data.status
            },


            // async accessCheck() {
            //     this.$store.commit('SET_LOADER', true);
            //     const res = await getAcessCheck.getStatus(
            //         this.$store.getters.INFOUSER.userInfo.login,
            //         this.$store.getters.INFOUSER.userInfo.pass,
            //         this.$store.getters.INFOUSER.id,
            //         this.$store.getters.INFOUSER.role,
            //         'administration',
            //         this.$store,
            //         this.$router,
            //     )
            //     if(res) {
            //         console.log(res.data.data)
            //     }
            // }
        },
        mounted() {
            this.getDataAdministration()
        },
    }
</script>

<style lang="less">
  #adminka {
    margin-top: 45px;
    height: auto;
    width: 100%;

    .adm {
      width: 100%;
      height: 100%;

      &-header {
        width: 100%;
        height: 50px;

        > span {
          padding: 5px 15px;
          font-size: 36px;
          color: black;
          letter-spacing: 0.02em;
        }
      }

      &-table {
        width: auto;
        height: auto;
        padding: 5px 15px;
        margin-right: 35px;
        overflow: auto;
        box-sizing: border-box;

        &__header {
          min-width: 1500px;
          width: auto;
          height: 40px;
          display: flex;

          > div {
            display: flex;
            justify-content: center;
            align-items: center;
          }

          &-FIO {
            width: 25%;
            min-width: 300px;
          }

          &-login {
            width: 20%;
            min-width: 250px;
          }

          &-mail {
            width: 20%;
            min-width: 250px;
          }

          &-phone {
            width: 10%;
            min-width: 150px;
          }

          &-status {
            width: 5%;
            min-width: 150px;
          }

          &-position {
            width: 10%;
            min-width: 200px;
          }

          &-role {
            width: 10%;
            min-width: 200px;
          }
        }

        &__body {
          width: auto;
          min-width: 1500px;
          height: auto;
          min-height: 400px;
        }

        &__pagination {
          margin-top: 15px;
          width: 100%;
          height: auto;
          display: flex;
          justify-content: center;
          align-items: center;
        }
      }
    }
  }
</style>



