<template>
    <div id="newTest">
      <div class="newTest">
        <div class="newTest__header">
          <span>СОЗДАТЬ НОВЫЙ ТЕСТ</span>
        </div>
      </div>

    </div>
</template>

<script>
    export default {
        name: "newTest",
        mounted() {
            this.$store.commit('SET_LOADER', false);
        }
    }
</script>

<style lang="less">
  #newTest {
    width: 100%;
    height: auto;
    margin-top: 45px;
    .newTest {
      width: 100%;
      height: 100%;
      &__header {
        width: 100%;
        height: 50px;
        > span {
          padding: 5px 15px;
          font-size: 36px;
          color: black;
          letter-spacing: 0.02em;

        }
      }
    }
  }
</style>



