<template>
  <div id="wikiInnovation" :style="{color: themeActiveComp ? themeActiveComp.workspaceBackground.backgroundAll : '#1e1e1e'}">
    <div class="innovation">
      <div class="innovation__header">
        <span>{{ _('innovations') }}</span>
      </div>
      <div class="innovation__body">

      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: "innovation",
    methods: {
      getData() {
        this.get_access('wiki/innovation')
      }
    },
    mounted() {
this.getData()
    }
  }
</script>

<style lang="less">
  #wikiInnovation {
    width: 100%;
    height: auto;
    margin-top: 45px;

    .innovation {
      width: 100%;
      height: 100%;

      &__header {
        width: 100%;
        height: 50px;

        > span {
          padding: 5px 15px;
          font-size: 36px;
          letter-spacing: 0.02em;
          text-transform: capitalize;

        }
      }
    }
  }
</style>



